import { ChangeDetectionStrategy, Component, Input, OnInit,  } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
//
import { TranslateService } from '@ngx-translate/core';
//
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';
import { Brand } from '../../models/offer';
import { Shop } from '../../../ms-shops/models/shops';

@Component({
    selector: 'offer-form',
    templateUrl: './offer-form.component.html',
    styleUrls: ['./offer-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class OfferFormComponent extends BaseReactiveFormComponent<Brand> implements OnInit {

    release: FormControl;

    @Input() shops: Array<Shop>;

    displayedColumns: string[] = [
        'checkbox',
        'text',
        'url',
        'actions'
    ];    

    links: any[] = [{ text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }, { text: "lalala", url: "url" }]

    constructor(
        public formBuilder: FormBuilder,
        translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);TODO
    }

    ngOnInit() {
        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {
        this.formGroup = new FormGroup({
            shop: new FormControl(this.data.shop, [Validators.required]),            
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.accept.emit(this.data);
        } else {
            this.triggerValidation();
        }
    }
}

