import { Component, AfterViewInit, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
//
import { MatDialog, MatDialogRef } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
//
import { ConfirmDialogComponent } from '../../../../../ui/modules/confirm-dialog/components/confirm-dialog/confirm-dialog.component';
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { ToastrService } from '../../../../../error-handling/services/toastr.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';


//import { setTranslations } from '../../../../../ngx-translate';
//import { TRANSLATIONS } from './i18n/delete-user.component.translations';
import { Brand } from '../../models/offer';
import { OffersService } from '../../services/offers.service';

const titleKey = 'Title Delete Brand';

const deleteBtnKey = 'Delete Brand';

const messageKey = 'Delete Brand Message';

const errorKey = 'Error';

const deletedBrandMessageKey = 'Deleted Brand Message';

@Component({
  selector: 'delete-offer',
  templateUrl: './delete-offer.component.html',
  styleUrls: ['./delete-offer.component.scss']
})
export class DeleteOfferComponent implements AfterViewInit, OnInit {

  data: Brand;

  modalRef: MatDialogRef<ConfirmDialogComponent>;

  brandId: string;

  constructor(
    public activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    public offersService: OffersService,
    private errorHandlingService: ErrorHandlingService,
    public router: Router,
    private translate: TranslateService,
    private toastr: ToastrService) {
    //setTranslations(this.translate, TRANSLATIONS);
  }

  ngAfterViewInit() {
    this.getCollection();
  }

  ngOnInit() {
    this.brandId = this.activatedRoute.snapshot.data.brandId;
  }

  getCollection() {
    this.offersService.getBrand(this.brandId).subscribe(response => {
      this.data = response.data;
      this.confirmDeleteBrand();
    },
      (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error)
    )
  }

  close(){
    this.router.navigate(this.activatedRoute.snapshot.data.closeRouteCommand, {relativeTo: this.activatedRoute});
  }

  confirmDeleteBrand() {
    this.modalRef = this.dialog.open(ConfirmDialogComponent, {
      data: {
        titleKey: titleKey,
        okBtnKey: deleteBtnKey,
        messageKey: messageKey,
        messageParam: { param: this.data.name }
      }
    });
   
    this.modalRef.afterClosed().subscribe(result => {
      if (result) {
        this.deleteBrand();
      } else {
        this.close();
      }
    });
  }

  deleteBrand(): void {
    this.offersService.deleteBrand(this.data.id).subscribe(response => {
      this.offersService.reloadBrands().subscribe(response => {
        this.offersService.brandsList.next(response);
        this.toastr.success(deletedBrandMessageKey);
        this.close();
      },
        (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error));
    },
      (error: HandledError) => {
        this.errorHandlingService.handleUiError(errorKey, error);
        this.close();
      }
    )
  }
}



