import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
//
import { MatPaginator, MatSort } from '@angular/material';
import { debounceTime } from 'rxjs/operators';
import { Subscription } from 'rxjs';
//
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
//
import { OffersService } from "../../services/offers.service";
import { Brand, BrandsListResponse } from "../../models/offer";
import { Collection } from '../../../ms-collections/models/collection';

const errorKey = 'Error';

@Component({
    selector: 'offers-table',
    templateUrl: './offers-table.component.html',
    styleUrls: ['./offers-table.component.scss']
})
export class OffersTableComponent implements AfterViewInit, OnInit {

    displayedColumns: string[] = [
        'name', 'description', 'imgUrl', 'actions'
    ];

    collections: Array<Collection>;

    filter: FormGroup;

    filterValueChanges: Subscription;

    @ViewChild(MatPaginator) paginator: MatPaginator;

    @ViewChild(MatSort) sort: MatSort;

    totalLength: number = 0;

    brandsList: Subscription;

    brands: Array<Brand> = [];

    constructor(
        public activatedRoute: ActivatedRoute,
        public offersService: OffersService,
        public errorHandlingService: ErrorHandlingService
    ) {
    }

    ngOnInit() {
        this.filter = this.createFilterFormGroup();
        this.filterValueChanges = this.filter.valueChanges.pipe(debounceTime(500)).subscribe(change => this.onFilter());
        this.paginator.pageIndex = 0;

        this.collections = this.activatedRoute.snapshot.data.collections;

        // Begin observing style list changes.
        this.brandsList = this.offersService.brandsList.subscribe((brandsList: any) => {
            this.totalLength = brandsList.dataCount;
            this.brands = brandsList.data;
            if (this.brands.length === 0 && this.totalLength > 0 && this.offersService.previousPageSize > 0) {
                this.offersService.previousPageIndex =
                    Math.ceil(this.totalLength / this.offersService.previousPageSize) - 1;
                this.offersService.reloadBrands().subscribe(response => {
                    this.offersService.brandsList.next(response);
                },
                    (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error));
            }
        });
    }

    ngAfterViewInit() {
        this.loadPage();
    }

    ngOnDestroy() {
        this.brandsList.unsubscribe();
        this.filterValueChanges.unsubscribe();
    }

    createFilterFormGroup() {
        let group: any = {};
        group['name'] = new FormControl('');
        group['collection'] = new FormControl('');
        return new FormGroup(group);
    }

    loadPage() {
        this.offersService.getBrands(
            Object.assign({}, this.filter.value),
            this.sort.active, this.sort.direction,
            this.paginator.pageIndex, this.paginator.pageSize).subscribe((response: BrandsListResponse) => {
                this.offersService.brandsList.next(response);
            },
                (err: HandledError) => {
                    this.errorHandlingService.handleUiError(errorKey, err)
                });
    }

    onFilter() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onSort() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onPage() {
        this.loadPage();
    }

}

